
.controller {
    background: #1C1E22;
    padding: 100px 0;
    &__block {
        height: 1074px;
        width: 494px;
        border-radius: 60px; 
        border-top: 2px solid#424750;
        border-image: round;
        background: linear-gradient(180deg, #353A40 0%, #16171B 100%);
        margin: 0 auto;
    }
    &__top {
        display: flex;
        justify-content: space-between;
        padding: 43px 43px 40px 43px;
       
    }
    &__bottom{
        position: relative;
        height: 931px;
        padding: 32px 40px;
        border-radius: 60px;
        background: linear-gradient(180deg, #353A40 0%, #16171B 100%);
        border-top: 2px solid#424750;
        border-bottom: 2px solid;
        border-image: round;
        
        &:after {
            position: absolute;
            content: '';
            top: 16px;
            left: 50%;
            transform: translateX(-50%);
            width: 60px;
            height: 4px;
            background: #17181C;
            box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.25) inset;
            border-radius: 20px;
        }
    }

&__ac {
    height: 345px;
    width: 375px;
    margin: 0 auto 56px;
}
&__fan-speed {
    height: 102px;
    width: 416px;
    margin: 0 auto 56px;
    h3 {
        text-align: center;
    }
}
.mode {
    &__icon {
        @include svg-c;
        fill: #fff;
        width: 17px;
        height: 18px;
    }
    .auto {
        width: 40px;
        height: 40px;
    }
    h3 {
        text-align: center;
        line-height: 29px;
        margin-bottom: 24px;
    }
    p {
        text-align: center;
        line-height: 22px;
        margin-bottom: 16px;
    }
    &__list {
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: space-between;
        li {
            list-style: none;
        }
        }
    }
    .info {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }
    .btn__wrap {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 63px;
        height: 63px;
        background: linear-gradient(318.21deg, #2C3036 14.05%, #31343C 85.29%);
        border-radius: 100%;
        box-shadow: -7px -7px 30px 0px #485057, 7px 7px 30px 0px #1F2427;

        &:after { 
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            content: '';
            width: 61px;
            height: 61px;
            background: linear-gradient(318.21deg, #131314 14.05%, #31343C 85.29%);
            box-shadow: -7px -7px 30px 0px #485057;
            box-shadow: 7px 7px 30px 0px #1F2427;
            filter: blur(4px);
            border-radius: 100%;
        }

    }
    .btn-sm {
        position: relative;
        background: linear-gradient(320.66deg, #2F353A 14.75%, #1C1F22 84.81%);
        border: transparent;
        height: 57px;
        width: 57px;
        border-radius: 50%;
        cursor: pointer;
       
        &:after {border: 1px solid #7F8489;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            content: '';
            background: linear-gradient(320.66deg, #2F353A 14.75%, #1C1F22 84.81%);
            width: 57px;
            height: 57px;
             filter: blur(2px); 
            border-radius: 50%;
            z-index: -1;
        }
        .menu {
            @include svg-c;
            fill:#7F8489;
            width: 16px;
            height: 9px;
        }
        .admin {
            @include svg-c;
            width: 13.5px;
            height: 15px;
            fill: transparent;
            stroke:#7F8489;
        }
    }
    h3 {
        font-family: Lato, sans-serif;
        font-size: 24px;
        font-weight: 900;
        line-height: 29px;
        color: #ffffff;
        margin-bottom: 8px;
    }
    p {
        font-family: Lato, sans-serif;
        font-size: 18px;
        font-weight: 400;
        line-height: 22px;
        color: #7F8489;
    }
}
.btn-lg__wrap {
    position: relative;
    width: 78.50px;
    height: 78.50px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow:-7px -7px 30px 0px rgba(38,46,50, 1), 14px 14px 40px 0px rgba(16,16,18, 0.75);
    border-radius: 100px;
    &:after {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: inline-block;
        width: 80px;
        height: 80px;
        border-radius: 100%;
        background: linear-gradient(318.21deg, #1D2328 14.05%, #131314 85.29%);
    }
}
.btn-lg {
    width: 72px;
    height: 72px;
    background: linear-gradient(320.66deg, #2F353A 14.75%, #1C1F22 84.81%);
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    border: 0;
    box-shadow: 0px 0px 3px 0px rgba(63, 63, 63, 0.45) inset;
    z-index: 10;
    cursor: pointer;
    &:hover {
    border: 0;
    background: linear-gradient(320.66deg, #2A87D3 14.75%, #1F66A4 45.12%, #113C67 84.81%);

        img {
            transform: rotate(180deg);
        }
    }
   
    &:after {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: inline-block;
        width: 72px;
        height: 72px;
        box-shadow: 0px 0px 3px 0px rgba(63, 63, 63, 0.45) inset;
        filter: blur(1px);
        z-index: 10;
        border-radius: 100%;
    }
}
.btn-lg-off__wrap {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    border-radius: 100px;
    box-shadow: -10px -15px 40px 0px rgba(82, 89, 94, 0.5), 10px 15px 40px 0px rgba(0, 0, 0, 0.5);
    &:after {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%; 
        filter: blur(1px);
        transform: translate(-50%, -50%);        
    }
}
.btn-lg-off {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 74px;
    height: 74px;
    border-radius: 74px;
    border: 3px solid rgba(5,141,217,1);
    background: linear-gradient(320.66deg, #005EA3 14.75%, #11A8FD 84.81%);
    transform: rotate(-180deg);
    
    .power {
        @include svg-c;
        width: 18px;
        height: 20px;
        fill: #ffffff;
        transform: translate(-50%,-50%) rotate(180deg);
    }
   
}
.controller__ac {
    margin-top: 66px;
}
.ac__block {
    position: relative;
    height: 24rem;
    width: 24rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    > span {
        position: absolute;
        content: '';
        width: 3px;
        height: 15px;
        border-radius: 25%;
        top: 50%;
        left: 50%;
        background: hsl(202, 89%, 50%);
        box-shadow: 0px 0px 10px 0px #0A8ADA;
        z-index: 100;
        &:nth-child(1) {
            transform: rotate(-90deg) translate(12em) rotate(-90deg); 
        }
        &:nth-child(2) {
            transform: rotate(-45deg) translate(12em) rotate(90deg); 
        }
        &:nth-child(3) {
          transform: translate(12rem) rotate(-90deg);
          background: #15171C;
          box-shadow: -0.7px 0px #535960;
        }
        &:nth-child(4) {
            transform: rotate(45deg) translate(12em) rotate(90deg); 
            background: #15171C;
            box-shadow: -0.7px 0px #535960;    
        }
        &:nth-child(5) {
            transform: rotate(90deg) translate(12em) rotate(-90deg); 
            display: none;
        } 
        &:nth-child(6) {
            transform: rotate(-45deg) translate(-12rem) rotate(90deg);
        }
        &:nth-child(7) {
            transform: translate(-12rem) rotate(90deg);
        }
        &:nth-child(8) {
            transform: rotate(225deg) translate(12em) rotate(-90deg); 
        }

    }
}
.ac {
    position: relative;
    height: 323px;
    width: 323px;
    border-radius: 100%;
    margin: 0 auto;
    background: linear-gradient(318.21deg, #202428 14.05%, #131314 85.29%);
    box-shadow: 20px 20px 70px 0px #141415, -20px -20px 70px 0px #485057;
    &::after {
        position: absolute;
        content: '';
        top: 12.5px;
        left: 12.5px;
        background-image: url('../img/path-blue.svg');
        background-repeat: no-repeat;
        background-size: cover;
        // fill: url("img/sprite.svg#paint0_angular") ;
        height: 300px;
        width: 268px;
        z-index: 10;
    }
    &::before{
        position: absolute;
        content: '';
        top: 12.5px;
        right: 12.5px;
        background-image: url('../img/path-black.svg');
        background-repeat: no-repeat;
        background-size: cover;
        height: 300px;
        width: 268px;
        z-index: 1; 
    }
    &__elippse {
        position: relative;
        height: 50px;
        width: 50px;
        left: 229px;
        top: 55px;
        border-radius: 30px;
        background: linear-gradient(320.66deg, #141515 14.75%, #2E3236 84.81%);
        border: 1px solid #1B1D1E;
        box-shadow: 4px 6px 20px 0px rgba(0, 0, 0, 0.35);
        z-index: 100;
        transform: rotate(180deg);
        &:after {
            position: absolute;
            content: '';
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background: linear-gradient(320.66deg, #0172BE 14.75%, #0F9BEE 84.81%);
            width: 4px;
            height: 4px;
            border-radius: 100%;
        }
    }
}
.ac__title {        
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: Gilroy, sans-serif;
    color: #ffffff;
    z-index: 100;
    .ac__deg {
        position: relative;
        font-size: 40px;
        font-weight: 900;
        line-height: 50px;
        letter-spacing: 0.01em;
        text-transform: uppercase;
            span {
                position: relative;
                display: inline-block;
                margin-right: 11px;
                &:after {
                display: inline-block;
                position: absolute;
                content: '';
                top: 10px;
                right: -10px;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                border: 3px solid #FFFFFF;
            }
        }
    }
    > span {
        font-size: 18px;
        font-weight: 500;
        line-height: 14px;
        text-align: center;
        display: block;
    }
}

.fan {
    h3 {
        margin-bottom: 26px;
    }
    &__slider {
        margin-top: 12px;
        background: #202226;
        box-shadow: -2px -2px 2px 0px rgba(78, 81, 84, 1) inset, 0px 4px 4px 0px rgba(0, 0, 0, 0.75) inset;
        border-radius: 0;
        border: 0;
        height: 9px;
        
        .noUi-handle {
            height: 28px;
            width: 28px;
            border-radius: 100%;
            background: linear-gradient(320.66deg, #141515 14.75%, #2E3236 84.81%);
            border: 1px solid rgba(40, 43, 46, 1);
            box-shadow: 4px 6px 20px 0px rgba(0, 0, 0, 0.35);
            transform: rotate(180deg);
            top: -10px;
            &:after {
                display: none;
            }
            &:before {
                @include svg-c;
                width: 4px;
                height: 4px;
                border-radius: 100%;
                background: linear-gradient(320.66deg, #0172BE 14.75%, #0F9BEE 84.81%);

            }
        }
        .noUi-active {
            box-shadow: 4px 6px 20px 0px rgba(0, 0, 0, 0.35);
            border: 0;
        }
        .noUi-connect {
            background: linear-gradient(180deg, rgba(0, 0, 0, 0.02) 48.67%, rgba(255, 255, 255, 0.04) 59.86%),
            linear-gradient(89.97deg, #016BB8 2.65%, #0782DB 30.1%, #0F9CEB 59.17%, #11A8FD 101.95%);
             
        }
        
    }
    &-label__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0;
        padding: 0;
        li {
            list-style: none;
            font-family: Lato, sans-serif;
            font-size: 14px;
            font-weight: 400;
            line-height: 17px;
            color:  #7F8489;

        }
    }
}

.active {
    border: 3px solid #058dd9;
    background: linear-gradient(320.66deg,#005ea3 14.75%,#11a8fd 84.81%);
    transform: rotate(-180deg);
    svg {
        transform: translate(-50%,-50%) rotate(180deg)!important;
    }
    &:hover {
        border: 3px solid #058dd9;
        background: linear-gradient(320.66deg,#005ea3 14.75%,#11a8fd 84.81%);
        transform: rotate(-180deg);
    }
}